'use strict';

import React from 'react';

const Home = React.createClass({

  render() {
    return (
      <input type="text" onChange={this.textInputDidChange}/>
    );
  },

  textInputDidChange(e) {
    console.log(e.currentTarget.value);
  }

});


React.render(
  <Home />,
  document.getElementById('main')
);



